body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.icon>svg {
	height: 13px;
    width: 13px;
	stroke-width: 1.1px;
	transform: scaleY(0.85);
}

.icon {
	position: absolute;
	top: calc(50% - .3575em);
	right: 0;
}

.filterSectionTitle {
	display: flex;
	align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 24px 0 8px;
    margin: -24px 0 -8px;
    position: relative;
}

.filterSection {
	padding: 16px 0;
    border-bottom: 1px solid #e5ebf0;
}

#sidebar{
  float: left;
  width: 240px;
  will-change: min-height;
}

#sidebar .sidebar__inner {
  padding: 10px;
  position: relative;
  transform: translate(0, 0); /* For browsers don't support translate3d. */
  transform: translate3d(0, 0, 0);
  will-change: position, transform;
}

#content {
  margin-left: 250px;
  padding: 10px;
}

#main-content p {
  font-size: 1em;
}

#main-content td {
  font-size: 0.9em;
}

#main-content .form-group {
  margin-bottom: 0;
  padding-bottom: 0;
}

label {
  margin-left: 5px;
}

.anchor {
    position: absolute;
    margin-top: -80px
}

:root {
  --color-calendar-graph-day-L1-bg: #9be9a8;
  --color-calendar-graph-day-L2-bg: #40c463;
  --color-calendar-graph-day-L3-bg: #30a14e;
  --color-calendar-graph-day-L4-bg: #216e39;
}

.big-details {
    display: block;
    font-weight: 200;
    font-size: 75px;
    line-height: 80px;
    letter-spacing: -2px;
    color: #e91e63;
}

.small-title {
    font-weight: 700;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: #aaa;
}

.col-xs-1 {
  flex: 0 0 auto;
  width: 8.3333333333%;
}

.col-xs-2 {
  flex: 0 0 auto;
  width: 16.6666666667%;
}

.col-xs-4 {
  flex: 0 0 auto;
  width: 33.3333333333%;
}

.col-xs-10 {
  flex: 0 0 auto;
  width: 83.3333333333%;
}

#mytable td, #mytable td a{
  font-size: 100%;
}

.toolTip {
  opacity: 1;
  background-color: #fff;
  border-radius: 5px;
  -webkit-box-shadow: 0 3px 3px 1px rgba(0, 0, 0, .11);
  box-shadow: 0 3px 3px 1px rgba(0, 0, 0, .11);
  border: 1px solid #d0cccc;
  padding: 0.5rem;
}